import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddBannerComponent } from '../add-banner/add-banner.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../layouts/layout.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DefaultComponent } from '../../../default/default.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { QuillModule } from 'ngx-quill';

const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: AddBannerComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), QuillModule,
        LayoutModule, FormsModule, ReactiveFormsModule, NgxPaginationModule, NgMultiSelectDropDownModule,
    ],
    declarations: [AddBannerComponent]
})
export class AddBannerModule { }
